.button {
  display: block;
  border-radius: 60px;
  background-color: #EE3030;
  font-size: 16px;
  color: #ffffff;
  text-decoration: none;
  cursor: pointer;
  max-width: 220px;
  margin: 0 auto;
  width: 100%;
  padding: 13px 10px;
  box-sizing: border-box;
  text-align: center;
  border: none;
  position: relative; }
  .button.button_default {
    display: inline-block;
    border-radius: unset;
    background: 0 0;
    color: #EE3030;
    text-align: left;
    padding: 0 0 0 25px;
    margin: 20px 0 30px; }
    @media screen and (min-width: 1080px) {
      .button.button_default {
        margin: 30px 0 60px; } }
    .button.button_default:before {
      content: '';
      position: absolute;
      width: 4px;
      height: 4px;
      border-top: 1.5px solid #EE3030;
      border-right: 1.5px solid #EE3030;
      top: 50%;
      left: 1px;
      transform: translateY(-50%) rotate(-135deg); }
  .button.button_black {
    max-width: 100%;
