  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  z-index: 1; }
  @media screen and (min-width: 1200px) {
    .container {
      padding-top: 80px;
      padding-bottom: 80px; } }
  .container.angle:before, .container.angle:after {
    content: '';
    position: absolute;
    width: 35%;
    height: 100%;
    z-index: -1;
    background-color: #F3F3F3;
    box-shadow: 0px 4px 60px rgba(0, 0, 0, 0.1); }
  .container.angle:after {
    left: 0;
    top: 0;
    transform: skew(-45deg);
    transform-origin: top left; }
  .container.angle:before {
    bottom: 0;
    right: 0;
    transform: skew(-45deg);
    transform-origin: bottom right; }
  .container.angle_small:before, .container.angle_small:after {
    width: 25%; }
  .container.angle_small:after {
    background: linear-gradient(120deg, #333333 0.72%, #1A1A1A 99.59%); }
  .container.angle_small:before {
    background: linear-gradient(0deg, #333333 0.72%, #1A1A1A 99.59%); }
